import i18next from 'i18next';
import LanguageDetector from 'i18next-browser-languagedetector';

const resources = {
  en: {
    translation: {
      sections: {
        miyabi: 'MIYABI',
        yaoyorozu: 'YAOYOROZU',
        about: 'ABOUT'
      }
    }
  },
  ja: {
    translation: {
      sections: {
        miyabi: '雅',
        yaoyorozu: '八百万',
        about: 'ABOUT'
      }
    }
  }
};

export const initializeI18n = async () => {
  await i18next
    .use(LanguageDetector)
    .init({
      resources,
      fallbackLng: 'en',
      detection: {
        order: ['querystring', 'navigator'],
      }
    });

  return i18next;
};
