import i18next from 'i18next';

export const initializeGallery = () => {
  const sections = ['miyabi', 'yaoyorozu', 'about'];
  const navContainer = document.querySelector('.nav-tabs');
  const galleryContainer = document.querySelector('.gallery-container');
  const modal = document.querySelector('.modal');

  // Create navigation tabs
  sections.forEach(section => {
    const navItem = document.createElement('div');
    navItem.classList.add('nav-item');
    navItem.dataset.section = section;
    navItem.textContent = i18next.t(`sections.${section}`);
    navContainer.appendChild(navItem);
  });

  // Set initial active section
  const firstNavItem = navContainer.querySelector('.nav-item');
  if (firstNavItem) {
    firstNavItem.classList.add('active');
    loadSectionContent(firstNavItem.dataset.section);
  }

  // Navigation click handler
  navContainer.addEventListener('click', (e) => {
    const navItem = e.target.closest('.nav-item');
    if (!navItem) return;

    // Update active state
    navContainer.querySelectorAll('.nav-item').forEach(item => {
      item.classList.remove('active');
    });
    navItem.classList.add('active');

    // Load section content
    loadSectionContent(navItem.dataset.section);
  });

  // Modal handlers
  function openModal(imageSrc) {
    const modalContent = modal.querySelector('.modal-content');
    modalContent.innerHTML = `
      <img src="${imageSrc}" alt="NFT">
      <button class="close-button"></button>
    `;
    modal.classList.add('active');
  }

  function closeModal() {
    modal.classList.remove('active');
  }

  modal.addEventListener('click', (e) => {
    if (e.target === modal || e.target.closest('.close-button')) {
      closeModal();
    }
  });

  // Handle escape key
  document.addEventListener('keydown', (e) => {
    if (e.key === 'Escape' && modal.classList.contains('active')) {
      closeModal();
    }
  });

  // Load section content
  function loadSectionContent(section) {
    // Clear current content
    galleryContainer.innerHTML = '';

    // Load and display NFTs based on section
    // This is where you would typically fetch and display your NFT data
    // For now, we'll just show a placeholder message
    if (section === 'about') {
      galleryContainer.innerHTML = '<div class="about-content"><h2>About Our NFT Gallery</h2><p>Coming soon...</p></div>';
    } else {
      // Example NFT display (replace with your actual NFT data)
      const dummyNFTs = Array(12).fill().map((_, i) => ({
        id: i + 1,
        image: `https://via.placeholder.com/300x300?text=NFT+${i + 1}`,
      }));

      dummyNFTs.forEach(nft => {
        const nftElement = document.createElement('div');
        nftElement.classList.add('gallery-item');
        nftElement.innerHTML = `<img src="${nft.image}" alt="NFT ${nft.id}">`;
        
        nftElement.addEventListener('click', () => {
          openModal(nft.image);
        });

        galleryContainer.appendChild(nftElement);
      });
    }
  }
};
